import React, { SyntheticEvent, memo } from "react";
import {
  InputAdornment,
  OutlinedInput,
  InputLabel,
  FormControl,
} from "@mui/material";
import { NumberSpinner } from "./NumberSpinner";
import { useNumeric, PASSABLE_NUMERIC_STATUSES } from "./useNumeric";
import { TextInput } from "./TextInput";
import { styled, alpha, lighten, darken } from '@mui/material/styles';

interface StyledNumberInputProps {
  error?: boolean
}

const StyledNumberInput = styled(TextInput)<StyledNumberInputProps>(
  {
    paddingRight: 30,
    'input[type="number"]::-webkit-inner-spin-button': {
      // WebkitAppearance: 'none',
      // margin: 0
    },
    'input[type="number"]::-webkit-outer-spin-button': {
      // WebkitAppearance: 'none',
      // margin: 0
    }
  }
)

interface NumberInputProps {
  disabled?: boolean,
  label?: string,
  onChange?: (value?: number) => void,
  step?: number,
  value?: number,
  onBlur?: (event: SyntheticEvent) => void,
  onFocus?: (event: SyntheticEvent) => void,
  onMouseEnter?: (event: SyntheticEvent) => void,
  onMouseLeave?: (event: SyntheticEvent) => void,
  suffix?: string,
  prefix?: string,
  min?: number,
  max?: number
}

export const NumberInput = memo(
  ({
    disabled,
    label,
    onChange = (_) => {},
    step,
    value = 0,
    onBlur = (_) => {},
    onFocus = (_) => {},
    onMouseEnter = (_) => {},
    onMouseLeave = (_) => {},
    suffix = "",
    prefix = "",
    min = Number.NEGATIVE_INFINITY,
    max = Number.POSITIVE_INFINITY,
  }: NumberInputProps) => {
    const {
      textValue,
      status,
      onChange: onChangeInner,
      onStepUp,
      onStepDown,
    } = useNumeric({
      initial: value,
      stepSize: step,
      min,
      max,
      onValidChange: onChange,
    });

    return (
      <FormControl
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
        className="nodrag"
      >
        {/* <InputLabel
          className="nodrag"
          htmlFor="outlined-position-vector"
          color="primary"
          shrink
        >
          {label}
        </InputLabel> */}
        <StyledNumberInput
          // notched
          className="nodrag"
          // size="small"
          id="outlined-position-vector"
          // label={label}
          onFocus={onFocus}
          onBlur={onBlur}
          disabled={disabled}
          value={textValue}
          onChange={onChangeInner}
          style={{ paddingRight: 4 }}
          maxLength={15}
          // inputProps={{ min, max, className: "nodrag" }}
          // startAdornment={
          //   <InputAdornment position="start">{prefix}</InputAdornment>
          // }
          // endAdornment={
          //   <InputAdornment position="end">
          //     {suffix}
          //     <NumberSpinner
          //       disabled={disabled}
          //       above={value >= max}
          //       below={value <= min}
          //       onClickDown={onStepDown}
          //       onClickUp={onStepUp}
          //     />
          //   </InputAdornment>
          // }
        />
        <NumberSpinner
            style={{position:'absolute', right: 4, top: 6}}
            disabled={disabled}
            above={value >= max}
            below={value <= min}
            onClickDown={onStepDown}
            onClickUp={onStepUp}
              />
      </FormControl>
    );
  }
);
